<template>
<div>
	<div class="page-header">
		<mt-header fixed :title="order.OrderNumber">
			<router-link :to="{name: 'HistoryList'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>
	
	<div class="page-body">
		<table>
			<tr>
				<td>DATAWATCH SYSTEMS INC</td>
			</tr>
			<tr>
				<td>4401 EAST WEST HIGHWAY, #500</td>
			</tr>
			<tr>
				<td>BETHESDA,  MD 20814</td>
			</tr>
		</table>

		<c-order :order="order" :paid-type="paidType" :paid-number="paidNumber"></c-order>

		<div class="hide_print">
			<mt-button type="primary" @click.native="printClick">Print For Your Record</mt-button>
			<mt-button @click.native="cancelClick">Cancel</mt-button>
		</div>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import BizLogin from '../api/BizLogin.js';

import OrderDetail from '../components/OrderDetail.vue';

export default {
	name: 'HistoryDetail',
	data(){
		return {
			order: {},
			paidType: '',
			paidNumber: ''
		}
	},
	components:{
		'c-order' : OrderDetail
	},
	methods:{
		showNetworkError()
		{
			Toast({message: 'The Web Server may be too busy, please try again at a later time.', duration: 3000});
		},
		printClick()
		{
			window.print(); 
			return false;
		},
		cancelClick()
		{
			this.$router.push({ name: 'HistoryList' });
		}
	},
	created()
	{
		let orderNumber = this.$route.params.OrderNumber;

		Indicator.open();
		BizLogin.GetOrderInfo(orderNumber).then(res=>{
			if(!res.data)
			{
				MessageBox.alert('order does not exist.');
			}
			else
			{
				this.order = res.data;
				if(this.order.PONumber.length > 0)
				{
					this.paidType = 'PO Number';
					this.paidNumber = this.order.PONumber;
				}
				else
				{
					if(this.order.TransactionList.length > 0)
					{
						let paid = this.order.TransactionList[0].Notes.split('|');
						if(paid.length >= 2)
						{
							this.paidType = paid[0];
							this.paidNumber = paid[1];
						}
					}
				}
			}
			Indicator.close();
		}).catch(err=>{
			this.showNetworkError();
			console.log(err);
			Indicator.close();
		});
	}
}
</script>

<style scoped>
	@media print {
		.hide_print
		{
			display: none;
		}
	}
	.page-header{
		margin-bottom: 40px;
	}
</style>
