<template>
<div>
	<div class="page-header">
		<mt-header fixed :title="customer.Email">
			<router-link :to="{name: 'Dashboard'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>

	<div class="page-body">
		<mt-loadmore :autoFill="false" :top-method="loadTop" @top-status-change="handleTopChange" 
			:bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded"  ref="loadmore">
			
			<div v-for="item in listArray" :key="item.OrderPK">
				<mt-cell>
					<span slot="title">
						<span class="OrderNumber">{{item.OrderNumber}}</span>
						<span class="OrderDate">{{calculateOrderDate(item)}}</span>
						<span class="OrderTotal">{{calculateOrderTotal(item)}}</span>
					</span>
					<mt-button icon="more" @click="itemClick(item.OrderNumber)"></mt-button>
				</mt-cell>
			</div>

			<div slot="top" class="mint-loadmore-top">
				<span v-show="topStatus !== 'loading'" :class="{ 'rotate': topStatus === 'drop' }">↓</span>
				<span v-show="topStatus === 'loading'">Loading...</span>
			</div>

			<div slot="bottom" class="mint-loadmore-bottom">
				<span v-show="bottomStatus !== 'loading'" :class="{ 'rotate': topStatus === 'drop' }">↓</span>
				<span v-show="bottomStatus === 'loading'">Loading...</span>
			</div>
		</mt-loadmore>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import { Loadmore } from 'mint-ui';
import Order from '../api/Order.js'
import Utils from '../utils/index.js';

export default {
	name: 'HistoryList',
	data(){
		return {
			topStatus: '',
			bottomStatus: '',
			allLoaded: false,

			pageCount: 1,
			pageSize: 30,
			currentPage:1,

			listArray: [],
			customer: {}
		}
	},
	methods:{
		showNetworkError()
		{
			Toast({message: 'The Web Server may be too busy, please try again at a later time.', duration: 3000});
		},
		calculateOrderDate(order)
		{
			let date = new Date(order.OrderDate);
			return (date.getMonth() + 1) + "/" + date.getDate() + "/" + date.getFullYear();
		},
		calculateOrderTotal(order)
		{
			return (order.SubTotalAmount + order.TaxAmount + order.ShippingAmount).toFixed(2);
		},
		handleTopChange(status) 
		{
        	this.topStatus = status;
		},
		handleBottomChange(status) 
		{
        	this.bottomStatus = status;
        },
		loadTop()
		{
			this.getList();
		},
		loadBottom()
		{
			this.currentPage = this.currentPage + 1;
			this.getPageList();
		},
		getList()
		{
			Indicator.open();
			Order.GetCountByCustomer(this.customer.CustomerPK, this.customer.Email).then(res=>{
				let count = parseInt(res.data / this.pageSize);
				let mod = res.data % this.pageSize;
				if(mod > 0)
				{
					this.pageCount = count + 1;
				}
				else
				{
					this.pageCount = count ;
				}

				this.currentPage = 1;
				Order.GetByCustomer(this.customer.CustomerPK, this.customer.Email, this.currentPage-1, this.pageSize).then(res=>{
					this.listArray = res.data;
					if(this.currentPage >= this.pageCount)
					{
						this.allLoaded = true;
					}
					this.$refs.loadmore.onTopLoaded();
					Indicator.close();
				}).catch(err=>{
					this.showNetworkError();
					console.log(err);
					Indicator.close();
				});
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				Indicator.close();
			});
		},
		getPageList()
		{
			Indicator.open();
			Order.GetByCustomer(this.customer.CustomerPK, this.customer.Email, this.currentPage-1, this.pageSize).then(res=>{
				this.listArray = res.data;
				if(this.currentPage >= this.pageCount)
				{
					this.allLoaded = true;
				}
				this.$refs.loadmore.onBottomLoaded();
				Indicator.close();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				Indicator.close();
			});
		},
		itemClick(OrderNumber) {
			this.$router.push({name: 'HistoryDetail', params: {OrderNumber: OrderNumber}});
		}
	},
	mounted()
	{
		this.customer = Utils.getCustomer();
		this.getList();
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
	.OrderNumber {
		width: 30%;
		display: inline-block;
	}
	.OrderDate {
		width: 30%;
		display: inline-block;
	}
	.OrderTotal {
		width: 20%;
		display: inline-block;
		text-align: right;
	}
</style>
