<template>
<div>
	<h3>Shipping Address &#38; Billing Address</h3>
	<table style="width:100%;">
		<tr v-for="item in shipInfoList" :key="item.ShipInfo">
			<td>{{item.ShipInfo}}</td>
		</tr>
	</table>

	<table style="width:100%;">
		<tr v-for="item in billInfoList" :key="item.BillInfo">
			<td>{{item.BillInfo}}</td>
		</tr>
	</table>

	<h3>Payment Method</h3>
	<p>{{paidMethod}}</p>

	<h3>Shipping</h3>
	<p>{{shippingMethod}}</p>

	<h3>Order items</h3>
	<table style="width:100%;">
		<thead>
			<tr>
				<th>ID</th>
				<th>Name</th>
				<th>Quantity</th>
				<th>Price</th>
				<th>Total</th>
			</tr>
		</thead>
		<tbody>
			<tr v-for="item in order.OrderItemList" :key="item.OrderItemPK">
				<td>{{item.ProductID}}</td>
				<td>{{item.ProductName}}</td>
				<td align='center'>{{item.Quantity}}</td>
				<td align='right'>{{item.PaidPrice.toFixed(2)}}</td>
				<td align='right'>{{(item.Quantity * item.PaidPrice).toFixed(2)}}</td>
			</tr>
		</tbody>
		<tfoot>
			<tr>
				<td align='right' colspan="4">Subtotal:</td>
			    <td align='right'>{{SubtotalAmount.toFixed(2)}}</td>
			</tr>
			<tr>
				<td align='right' colspan="4">Tax</td>
			    <td align='right'>{{TaxAmount.toFixed(2)}}</td>
			</tr>
			<tr>
				<td align='right' colspan="4">Shipping (Include Tax)</td>
			    <td align='right'>{{ShippingAmount.toFixed(2)}}</td>
			</tr>
			<tr>
				<td align='right' colspan="4">Total:</td>
			    <td align='right'>{{TotalAmount.toFixed(2)}}</td>
			</tr>
		</tfoot>
	</table>
</div>
</template>

<script>
export default {
	name: 'OrderDetail',
	props:{
		order: {
			type: Object,
			required: true
		},
		paidType:{
			type: String,
			default: ''
		},
		paidNumber:{
			type: String,
			default: ''
		}
	},
	computed:{
		SubtotalAmount:{
			get: function(){
				let ret = 0;
				if(Object.keys(this.order).length > 0)
				{
					ret = this.order.SubTotalAmount;
				}

				return ret;
			}
		},
		TaxAmount:{
			get: function(){
				let ret = 0;
				if(Object.keys(this.order).length > 0)
				{
					ret = this.order.TaxAmount;
				}

				return ret;
			}
		},
		ShippingAmount:{
			get: function(){
				let ret = 0;
				if(Object.keys(this.order).length > 0)
				{
					ret = this.order.ShippingAmount;
				}

				return ret;
			}
		},
		TotalAmount:{
			get: function(){
				let ret = 0;
				if(Object.keys(this.order).length > 0)
				{
					ret = this.order.SubTotalAmount + this.order.TaxAmount + this.order.ShippingAmount;
				}

				return ret;
			}
		},
		shipInfoList:{
			get: function(){
				let ret = [];
				if(Object.keys(this.order).length > 0)
				{
					ret.push({ShipInfo: 'Ship To:'});
					ret.push({ShipInfo: this.order.ShipName});
					ret.push({ShipInfo: this.order.ShipAddress1});
					if(this.order.ShipAddress2.length > 0)
					{
						ret.push({ShipInfo: this.order.ShipAddress2});
					}	
					ret.push({ShipInfo: this.order.ShipCity + ', ' + this.order.ShipState + ' ' + this.order.ShipZipcode});
				}

				return ret;
			}
		},
		billInfoList:{
			get: function(){
				let ret = [];
				if(Object.keys(this.order).length > 0)
				{
					ret.push({BillInfo: 'Bill To:'});
					ret.push({BillInfo: this.order.BillName});
					ret.push({BillInfo: this.order.BillAddress1});
					if(this.order.BillAddress2.length > 0)
					{
						ret.push({BillInfo: this.order.BillAddress2});
					}	
					ret.push({BillInfo: this.order.BillCity + ', ' + this.order.BillState + ' ' + this.order.BillZipcode});
				}

				return ret;
			}
		},
		paidMethod:{
			get: function(){
				return this.paidType + ': ' + this.paidNumber;
			}
		},
		shippingMethod:{
			get: function(){
				let ret = '';
				if(Object.keys(this.order).length > 0)
				{
					if(this.order.Fed_X.length > 0)
					{
						ret = this.order.ShippingMethod + ' (Fed_X: '+ this.order.Fed_X + ')';
					}
					else
					{
						ret = this.order.ShippingMethod;
					}
				}

				return ret;
			}
		}
	}
}
</script>

<style scoped>
