	static ExportToExcel(criteria,pageIndex,pageSize)
	{
		let token = Utils.getUserToken();
		let para = {
			ParameterNames: 'criteria|pageIndex|pageSize',
			Parameters: [criteria,pageIndex,pageSize]
		};

		Object.assign(token, para);

		let url =  Utils.getBaseUrl() + 'DownloadService/ExportToExcel?tableName=Order';
		return axios.post(url, token, { responseType: 'blob' });
	}

	static ImportFromExcel(file)
	{
		let token = Utils.getUserToken();
		let formData = new FormData();
		formData.append('UserId', token.UserId);
		formData.append('Password', token.Password);
		formData.append('Ticks', token.Ticks);
		formData.append('file', file);

		let url =  Utils.getBaseUrl() + 'UploadService/ImportFromExcel?tableName=Order';
		return axios.post(url, formData, {headers:{'Content-Type': 'multipart/form-data'}});
	}

